// custom checkbox
.checkbox--styled {
    display: inline-block;
    position: relative;
    text-align: left;
    cursor: pointer;
    font-size: 1rem;
    width: 18px;
    transition: all .3s ease;

    & > input {
        position: absolute;
        z-index: -1;
        opacity: 0;

        & ~ span {
            border: 1px solid #c8cfdd !important;
            overflow: visible;
            background: 0 0;
            top: 0;
            border-radius: 3px;
            background: 0 0;
            position: absolute;
            top: 2px;
            left: 0;
            height: 18px;
            width: 18px;
            text-align: left;
            cursor: pointer;
            font-size: 1rem;
        }

        &:checked ~ span {
            border: 1px solid #c8cfdd;
            overflow: visible;
            background: 0 0;
            top: 0;
            border-radius: 3px;
            background: 0 0;
            position: absolute;
            top: 1px;
            left: 0;
            text-align: left;
            cursor: pointer;

            &::after {
                content: '';
                position: absolute;
                top: 50%;
                left: 50%;
                margin-left: -2px;
                margin-top: -6px;
                width: 5px;
                height: 10px;
                border-width: 0 2px 2px 0 !important;
                transform: rotate(45deg);
                border: solid #bfc7d7;
                box-sizing: border-box;
            }
        }
    }
}
//custom checkbox end
