$column-size: 9;
$cyan: #1890ff;
$light-gray: #d9d9d9;
$white: #fff;
$border-radius: 5px;

.table--titlebar {
    display: flex;
    justify-content: space-between;
    align-items: stretch;
    margin: 0;
    background-color: $white;
    border-bottom: 1px solid #ebedf2;
    min-height: 67px;
    border-top-right-radius: $border-radius;
    border-top-left-radius: $border-radius;

    &__title {
        display: inline-flex;
        align-items: center;
        margin: 0;
        padding: 0;
        font-size: 14px;
        font-weight: 500;
        color: #48465b;
        padding-left: 30px;
    }

    &__filter {
        // align-items: center; should not be used, it breaks dropdown and animation
        justify-content: center;
        display: inline-flex;

        &:hover {
            border: 0;
            border-top-right-radius: $border-radius;
            background-color: rgba(70, 62, 62, 0.075);
        }
    }
}
