$table-border-mobile: 1px solid rgba(51, 51, 51, 0.09);
$cyan: #1890ff;
$light-gray: #d9d9d9;
$white: #fff;
$border-radius: 5px;
$button-status-size: 12px;
$table-width: auto;

//temporary border-box fix
*,
*::before,
*::after {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

// //temporary body color
// body {
//     background-color: #f7f7fb;
// }

table {
  line-height: 1.2;
  border-collapse: collapse; // it is needed to style tr border
  padding-bottom: 50px;

  & .checkbox--styled {
    margin-bottom: 10px;
    margin-left: 20px;
  }
  & td,
  th {
    //fonts must be responsive for high resolutions
    // font-size: 1vw;
    transition: all 0.3s, border 0s;
    // max-width: 200px;
    word-wrap: break-word;
    vertical-align: baseline;
    // font-weight: 300;
    color: #595d6e;
    padding: 16px 16px;
    text-align: left;
    padding: 1.3rem;

    &:first-child {
      padding-left: 20px;
      // font-size: 15px;
      font-weight: 500;
      cursor: pointer;
    }
    &:last-child {
      padding-right: 0;
    }
  }
  & tr {
    border-bottom: 1px dashed #e8e8e8ce;
    padding: 50px;
    &:last-child {
      border: none;
    }
  }
  & th {
    padding-left: 0;
    font-weight: 500;
    color: #74788d;
    vertical-align: initial;
  }
}

tbody li:first-child {
  display: inline-flex;
  transition: all 0.25s;
  border-radius: $border-radius;
  flex-direction: column;
  margin: 100px;
}

.table--container {
  font-family: Poppins, Helvetica, sans-serif;
  display: inline-flex;
  // overflow-y: none;
  transition: all 0.25s;
  border-radius: $border-radius;
  flex-direction: column;
  // box-shadow: 0 0 13px 0 rgba(82, 63, 105, 0.05);
  width: $table-width;
  // padding-bottom: 100px;
  background-color: $white;
  margin: 7px 32px;
}

.table--inner--container {
  //old inline-flex
  display: flex;
  flex-flow: column wrap;
  background-color: $white;
  padding: 0 15px 20px 15px;
  // padding-top: 0;
  border-bottom-right-radius: 5px;
  border-bottom-left-radius: 5px;
  width: 100%;
}

td:first-child {
  color: #62bfff;
  font-weight: 400;
}

thead {
  // position: fixed;
  // top: 0;
  border-bottom: 2px solid #e8e8e8ce;
  td {
    padding-top: 5px;
    padding-bottom: 0;
  }
  .actionsTitle {
    font-weight: 500;
    color: #74788d;
    vertical-align: initial;
  }
}

tbody > tr {
  .actionBtns {
    display: flex;
    // flex-wrap: wrap;
    justify-content: space-around;
    button {
      font-size: 12px;
      padding: 6px 15px 8px 15px;
      border-radius: 7%;
      border: none;
    }
    &--edit {
      margin-left: -8px;
      background-color: #e6f8f3;
      color: #47cca5;
      &:hover {
        background-color: #0abb87;
        color: #ffffff;
      }
    }
    &--delete {
      background-color: #ffebf1;
      color: #fe8bb1;
      &:hover {
        background-color: #fd397a;
        color: #ffffff;
      }
    }
  }
  &:hover {
    background-color: #ebebeb;
    transition: background-color 0.2s ease-in;
  }
}

.tdBefore {
  display: none;
}

.page-item a {
  float: left;
  padding: 6px 14px;
  text-decoration: none;
  background-color: #e8e8ea;
  color: #000000;
  transition: color 0.3s;
  // border: 1px solid #ddd;
  font-size: 17px;
  margin-left: 5px;
  &:hover {
    background-color: #292a3d;
    color: #ffffff;
  }
}

.pagination {
  float: right;
  margin-right: 60px;
  margin-bottom: 50px;
  cursor: pointer;
  display: flex;
  overflow: none;
}

.active a {
  background-color: #ffb822;
  color: #000000;
}

@media screen and (max-width: 40em) {
  /*
      Force table elements to not behave like tables anymore
      Hide table headers (but not display: none;, for accessibility)
    */

  table,
  thead,
  tbody,
  th,
  td,
  tr {
    display: block;
  }

  .table--container {
    width: 100%;
    margin: 2px;
  }

  .table--inner--container {
    padding: 5px;
  }

  tbody tr {
    border: $table-border-mobile;
    padding: 0.25em;
    margin: 5px;

    &:last-child {
      border: $table-border-mobile;
    }
  }

  thead tr {
    display: none;
  }

  td {
    /* Behave like a "row" */
    border: none !important;
    position: relative;
    padding-left: calc(50% + 10px) !important;
    text-align: left !important;
    white-space: pre-wrap;
    overflow-wrap: break-word;
  }

  td .tdBefore {
    /* Now like a table header */
    position: absolute;
    display: block;

    /* Top/left values mimic padding */
    left: 1rem;
    width: calc(50% - 20px);
    white-space: pre-wrap;
    overflow-wrap: break-word;
    text-align: left !important;
    font-weight: 400;
  }
}
