$pink: #fc813a;
$orange: #fcbd78;
$faded-white: #fafafc;
$dark-grey: #e5e5e7;
$yellow: #efb05a;
$sidebar-width: auto;

.main--content {
  margin-left: 85px;
  color: white !important;
  transition: 300ms ease-in-out;
  & main {
    display: flex;
    flex-direction: column;
    height: 100vh;
    overflow-y: scroll;
    padding-bottom: 100px;
  }
  & .header {
    // border: 1px solid #e4e9f0;
    // border-top: none;
    // border-left: none;
    // background-color: white;
    // background-image: linear-gradient(120deg, #d4fc79 0%, #96e6a1 100%);
    // background-image: linear-gradient(to right, #43e97b 0%, #38f9d7 100%);
    background-color: #ffffff;
    height: 65px;
    box-sizing: border-box;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    justify-content: flex-end;
    & svg {
      padding: 16px;
      box-sizing: unset;
      &:hover {
        cursor: pointer;
      }
    }
    & img {
      margin-right: 16px;
    }
    & span {
      margin-right: 12px;
      color: #000000;
    }
    & span:first-child {
      flex-grow: 1;
      font-weight: 600;
      margin-left: 32px;
    }
    &--notification {
      margin-left: -29px;
      & div {
        background-color: #fc381d;
        position: absolute;
        width: 7px;
        height: 7px;
        top: 21px;
        right: 85px;
        border-radius: 50%;
        animation: shadow-pulse 1s infinite;
      }
    }
  }
  &__expanded {
    margin-left: 260px;
  }
}

* {
  font-family: system-ui;
  margin: 0;
  padding: 0;
}

body {
  height: 100%;

  background-color: #f6f6f9;
}

// .sidebar {
// }

.main-content {
  display: flex;
  flex-direction: column;
  padding: 0 32px 4rem;
  margin-left: 6rem;
  transition: 300ms ease-in-out;
  position: relative;
  z-index: 2;
  &--expanded {
    //?? more better solution?
    margin-left: 260px;
  }
}

.mainContent {
  width: 100%;
}
.main-content header {
  height: 5rem;
  border-radius: 5px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 1rem 0 2rem;
}

.main-content header span {
  width: 16rem;
  height: 2rem;
  background: $dark-grey;
}

.main-content header ul {
  list-style: none;
  display: flex;
  align-items: center;
}

.main-content header ul li {
  width: 8rem;
  height: 2rem;
  margin: 0 1rem;
  background: $dark-grey;

  &:last-child {
    margin-right: 0;
  }
}

.main-content .container {
  // height: 100%;
  display: flex;
  flex-wrap: wrap;
}

.main-content .module--full {
  width: 100%;
  height: 400px;
  box-shadow: 0 3px 8px rgba(lightgray, 0.2), 0 6px 7px rgba(lightgray, 0.2);
  background: white;
}

.main-content .module-wrapper {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 1rem;
}

.main-content .module--half {
  flex: 1;
  height: 400px;
  box-shadow: 0 3px 8px rgba(lightgray, 0.2), 0 6px 7px rgba(lightgray, 0.2);
  background: white;

  &:first-child {
    margin-right: 1rem;
  }
}

span.hide {
  display: none !important;
  color: red;
}

.searchbar {
  width: 736px;
  height: 48px;
  margin: 20px 32px;
  display: flex;
  &--input {
    width: 100%;
    height: 100%;
    background: #ffffff;
    border: 1px solid #e4e8ee;
    box-sizing: border-box;
    border-radius: 2px;
    padding-left: 16px;
  }
  &--search-icon {
    display: none;
  }
  & button {
    margin-left: 20px;
    height: 48px;
    width: 100px;
    border-radius: 0;
    font-family: sans-serif;
    font-weight: 600;
    color: #84848d;
  }
}
@keyframes shadow-pulse {
  0% {
    box-shadow: 0 0 0 0px rgba(235, 35, 35, 0.5);
  }
  100% {
    box-shadow: 0 0 0 25px rgba(255, 1, 1, 0);
  }
}
.table--titlebar {
  display: none !important;
}
