.sidebar {
  background-color: #000638;
  // height: 100vh; /*allows both columns to span the full height of the browser window*/
  display: flex;
  flex-direction: column; /*places the left and right headers above the bottom content*/
  user-select: none;
  // transition: all 1ms ease-in-out;
  white-space: nowrap;
  // old- 97
  width: 85px !important;
  position: absolute;
  transition: 300ms ease-in-out;
  height: 100vh;
  &:hover {
    background-position: 0 0;
  }
  & li {
    box-sizing: border-box;
    font-size: 15px;
    line-height: 64px; /* to make text appear middle, instead of vertical align */
    cursor: pointer;
    padding-left: 36px;
    font-family: Poppins;
    // border: 1px solid #555;
    display: flex;
    &:nth-child(2) {
      background: #171c4a;
      & span {
        color: #ffffff;
        font-weight: 500;
      }
      & svg {
        fill: green;
      }
    }
    & span {
      padding-right: 14px;
      color: #ffffff;
      &:nth-child(2) {
        margin-top: -2px;
      }
    }
    &:hover {
      background-color: #333860;
      transition: background-color 0.3s ease-in;
    }
  }
  &--expanded {
    // <---PROBLEM HERE--->
    //old 304
    width: 260px !important;
    & svg.sidebar--curve--svg {
      transition: all 300ms ease-in-out;
      //***there's just this is working
      margin-left: 555px;
    }
  }

  &--curve--svg {
    transition: all 300ms ease-in-out;
    // transition: margin 610ms; //uncomment for effect
    margin-left: -27px;
    width: 450px;
    zoom: 0.3;
    margin-top: 300px;
  }

  & p {
    padding-left: 29px;
    font-size: 13px;
    color: #ffffff;
    height: 40px;
    padding-top: 20px;
    box-sizing: border-box;
    margin-bottom: 20px;
  }

  &--logo__div {
    display: flex;
    padding-left: 25px;
    padding-top: 17px;
    padding-bottom: 17px;
    height: 71px;
    font-display: swap;
    box-sizing: border-box;
    // border-bottom: 1px solid #e4e9f0;
    & span {
      opacity: 0.2;
    }
    &--expanded span {
      opacity: 1;
      transition: all 300ms ease-in-out;
    }
  }
  &--logo__text {
    margin-top: 10px;
    letter-spacing: 1px;
    padding-left: 5px;

    & span:first-child {
      font-weight: 700;
      color: #ffffff;
      font-size: 30px;
      margin-top: -15px;
    }
    & span:last-child {
      font-size: 9px;
      float: right;
      color: #ffffff;
      padding-right: 3px;
      margin-top: -10px;
    }
  }
  & span {
    display: block;
    margin-bottom: 7px;
  }
}

.headerSidebar {
  color: #a19d9d;
  font-weight: bold;
  opacity: 0.4;
}

.sidebar button {
  color: #fafafa;
  font-weight: 800;
  font-family: Poppins, Helvetica, sans-serif;
  line-height: 1.5;
  position: relative;
  font-size: 20px;
  width: 40px;
  background-color: #00418c;
  height: 40px;
  border-radius: 50%;
  color: white;
  bottom: 103px;
  margin-left: -54px;
}

